.game_mobile_rank_portrait {
  animation: slide_in_from_bottom .4s 1;
  background-image: linear-gradient( rgb(50, 50, 50, 1) 10%,rgb(0, 0, 0, 1) 100%);
  border: 2px solid grey;
  box-shadow: 0 -10px 15px 0 rgba(0, 0, 0, 0.8), 0 0 15px 0 rgba(0, 0, 0, 0.8);
  margin: 0px auto 0px auto;
  padding: 10px;
  text-align: center;
  width: calc(300px + (500 - 300) * ((100vw - 500px) / (1920 - 500)));
}

.game_mobile_rank_portrait h1, .dismount_game_mobile_rank_portrait h1 {
  animation: text_shadow_blink 1.5s ease-in-out infinite;
  background-color: #000;
  border: 1px solid grey;
  border-radius: 5px;
  box-shadow: 0 0px 10px 0 rgba(0, 0, 0, 0.8), 0 0px 10px 0 rgba(0, 0, 0, 0.8);
  color: yellow;
  font-family: 'Conv_digital-7.mono';
  font-size: calc(23px + (33 - 23) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
  margin: 0 auto 0 auto;
  padding: 0px 10px 0px 10px;
  text-align: center;
  text-shadow: 0px 0px 2.5px yellow;
  width: calc(250px + (350 - 250) * ((100vw - 500px) / (1920 - 500)));
}

.game_mobile_rank_portrait h2, .dismount_game_mobile_rank_portrait h2 {
  color: white;
  font-family: "Staatliches";
  font-size: calc(20px + (30 - 20) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
  margin: 0 auto 0 auto;
  padding: 0px 10px 0px 10px;
  text-align: center;
}

.game_mobile_rank_landscape {
  align-self: center;
  animation: slide_in_from_bottom .4s 1;
  background-image: linear-gradient( rgb(50, 50, 50, 1) 10%,rgb(0, 0, 0, 1) 100%);
  border: 2px solid grey;
  border-radius: 0px 50px 0px 0px;
  box-shadow: 0 -10px 15px 0 rgba(0, 0, 0, 0.8), 0 0 15px 0 rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  min-height: 120px;
  text-align: center;
  width: 250px;
}

.game_mobile_rank_landscape h1, .dismount_game_mobile_rank_landscape h1 {
  animation: text_shadow_blink 1.5s ease-in-out infinite;
  background-color: #000;
  border: 1px solid grey;
  border-radius: 5px;
  box-shadow: 0 0px 10px 0 rgba(0, 0, 0, 0.8), 0 0px 10px 0 rgba(0, 0, 0, 0.8);
  color: yellow;
  font-family: 'Conv_digital-7.mono';
  font-size: calc(23px + (33 - 23) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
  margin: 0 auto 0 auto;
  text-align: center;
  text-shadow: 0px 0px 2.5px yellow;
}

.game_mobile_rank_landscape h2, .dismount_game_mobile_rank_landscape h2 {
  color: white;
  font-family: "Staatliches";
  font-size: calc(20px + (30 - 20) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
  margin: 0 auto 0 auto;
  text-align: center;
}