.dismount_countdown_header{
  animation: dismount_countdown_header 1s ease-in-out 1;
  background-image: linear-gradient( rgb(50, 50, 50, 1) 10%,rgb(0, 0, 0, 1) 100%);
  border: 2px solid grey;
  border-radius: 50px 50px 0px 0px;
  box-shadow: 0 -10px 15px 0 rgba(0, 0, 0, 0.8), 0 0 15px 0 rgba(0, 0, 0, 0.8);
  margin: 0px auto 0 auto;
  padding: 0px 0px 0px 0px;
  text-align: center;
  width: calc(300px + (500 - 300) * ((100vw - 500px) / (1920 - 500)));
}

@keyframes dismount_countdown_header {
  0% {
    transform: translateX(0px);
  }
  25% {
    transform: translateX(-2000px);
  }
  100% {
    transform: translateX(-2000px);
  }
}

.dismount_countdown_timer {
  align-items: center;
  align-content: center;
  animation: dismount_countdown_timer 1s ease-in-out 1;
  background-color: rgba(255,255,255, 1);
  background-image: linear-gradient( rgb(50, 50, 50, 1) 10%,rgb(0, 0, 0, 1) 100%);
  border-radius: 0px;
  display: flex;
  color: white;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  margin: 0 auto 0 auto;
  padding: 0px;
  text-align: center;
  width: calc(300px + (600 - 300) * ((100vw - 320px) / (1920 - 320)));
}

@keyframes dismount_countdown_timer {
  0% {
    transform: translateX(0px);
  }
  25% {
    transform: translateX(2000px);
  }
  100% {
    transform: translateX(2000px);
  }
}

.dismount_countdown_tutorial {
  animation: dismount_countdown_tutorial 1s ease-in-out 1;
  background-image: linear-gradient( rgb(50, 50, 50, 1) 10%,rgb(0, 0, 0, 1) 100%);
  border: 2px solid grey;
  border-radius: 0px 0px 50px 50px;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.8), 0 15px 15px 0 rgba(0, 0, 0, 0.8);
  display: flex;
  color: white;
  text-align: center;
  width: calc(300px + (600 - 300) * ((100vw - 320px) / (1920 - 320)));
}

@keyframes dismount_countdown_tutorial {
  0% {
    transform: translateX(0px);
  }
  25% {
    transform: translateX(-2000px);
  }
  100% {
    transform: translateX(-2000px);
  }
}